import { Component, OnInit, Inject } from '@angular/core';
import { FormControl, FormGroupDirective, NgForm, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { DialogData } from '../date-picker-and-time/date-picker-and-time.component';

export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}

@Component({
  selector: 'app-follow-dialog',
  templateUrl: './follow-dialog.component.html',
  styleUrls: ['./follow-dialog.component.css']
})
export class FollowDialogComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<FollowDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData
  ) { }

  emailFormControl = new FormControl('', [
    Validators.required,
    Validators.email,
  ]);

  matcher = new MyErrorStateMatcher();

  ngOnInit() {
  }

  closeDialog() {
    this.dialogRef.close();
  }

  checkEmail() {
    console.log('call checkEmail');
    console.log(this.emailFormControl);
    if (this.emailFormControl.value !== undefined && this.emailFormControl.value ) {
      console.log('can close:' + this.emailFormControl.value);
    } else {
      alert('Please enter correct Email');
    }
  }

}
