import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { NgbDateStruct, NgbCalendar } from '@ng-bootstrap/ng-bootstrap';
import { MatDialog } from '@angular/material';
import { DatePickerAndTimeComponent } from '../date-picker-and-time/date-picker-and-time.component';

@Component({
  selector: 'app-search-bar',
  templateUrl: './search-bar.component.html',
  styleUrls: ['./search-bar.component.css']
})
export class SearchBarComponent implements OnInit {
  options: FormGroup;
  value = '';
  preferConditions: boolean;
  parking: boolean;
  deliver: boolean;
  preFee: boolean;
  menuCode: number;
  hourNumber: number;
  minNumber: number;

  constructor(
    fb: FormBuilder,
    public dialog: MatDialog
    ) {
    this.options = fb.group({
      menuCodeControl: ['', Validators.required],
    });
  }

  ngOnInit() {
    this.parking = false;
    this.deliver = false;
    this.preFee = false;
    this.menuCode = this.options.controls.menuCodeControl.value;
  }

  openDateTimeDialog(): void {
    const dialogRef = this.dialog.open(DatePickerAndTimeComponent, {
      width: '500px',
      data: { hourNumber: this.hourNumber, minNumber: this.minNumber }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.minNumber = result.minNumber;
      this.hourNumber = result.hourNumber;
    });
  }


}
