import { Component, OnInit, Inject } from '@angular/core';
import { NgbCalendar, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';


export interface DialogData {
  hour: number;
  min: number;
}

@Component({
  selector: 'app-date-picker-and-time',
  templateUrl: './date-picker-and-time.component.html',
  styleUrls: ['./date-picker-and-time.component.css']
})
export class DatePickerAndTimeComponent implements OnInit {
  model: NgbDateStruct;
  date: { year: number, month: number };

  hourNumber: number;
  minNumber: number;
  time = { hour: 0, minute: 0 };
  meridian = true;

  constructor(
    private calendar: NgbCalendar,
    public dialogRef: MatDialogRef<DatePickerAndTimeComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
  ) { }


  ngOnInit() {

  }

  selectToday() {
    this.model = this.calendar.getToday();
  }
  toggleMeridian() {
    this.meridian = !this.meridian;
  }
  onNoClick(): void {
    this.dialogRef.close();
  }
}
